<template>
  <article>
    <p>
      <slot></slot>
    </p>
    <div class="tools" v-if="requestStatus === 'pending' && shownTools">
      <input type="text" v-model="reason" />
      <button class="completed" @click="answerRequest(true)">✔</button>
      <button class="rejected" @click="answerRequest(false)">✘</button>
    </div>
  </article>
</template>

<script>
import axios from "axios";
export default {
  name: "request",
  props: [
    "requestId",
    "requestType",
    "requestStatus",
    "telegramId",
    "requestDate",
  ],
  data() {
    return {
      reason: "",
      shownTools: true,
    };
  },
  methods: {
    async answerRequest(decision) {
      this.shownTools = false;
      let confirmationMessage = decision
        ? `تم تنفيذ طلبكم السابق الواقع في ${this.requestDate}`
        : `تم رفض طلبكم السابق الواقع في ${this.requestDate}`;
      try {
        let res = await axios.put(
          `${this.$store.state.server}/${this.requestType}/${this.requestId}`,
          {
            reason: this.reason,
          }
        );
        if (res.status == 200) {
          axios.post(
            `${this.$store.state.server}/broadcast/${this.telegramId}`,
            {
              confirmationMessage,
            }
          );
        }
      } catch (err) {
        console.log(err);
        alert(
          "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
        );
      }
    },
  },
};
</script>

<style lang="scss" scoped>
article {
  background: rgb(228, 228, 228);
  border-radius: 8px;
  padding: 10px;
  p {
    text-align: left;
    white-space: pre-wrap;
    line-height: 1.5em;
  }
  .tools {
    display: flex;
    flex-flow: row wrap;
    .completed {
      background: green;
    }
    .rejected {
      background: red;
    }
    .completed,
    .rejected {
      color: white;
      border-radius: 4px;
      border: none;
      cursor: pointer;
    }
  }
}
</style>
