<template>
  <div class="payments">
    <section>
      <request
        v-for="request in requests"
        :key="request"
        :requestId="request._id"
        :requestStatus="request.status"
        :requestDate="request.createdAt"
        :telegramId="request.telegramId"
        requestType="payments"
      >
        {{
          `username: ${request.username}
telegram id: ${request.telegramId}
number: ${request.number}
status: ${request.status}
provider: ${request.provider}
number type: ${request.numType}
amount: ${request.amount}
requested in: ${request.createdAt}`
        }}
      </request>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
import request from "../components/request.vue";
import playAlert from "alert-sound-notify";
export default {
  name: "Payments",
  data() {
    return {
      requests: [],
    };
  },
  components: { request },
  methods: {
    async getRequests() {
      let rs0 = await axios.get(`${this.$store.state.server}/payments/0`);
      if (
        !this.requests[0] ||
        this.requests[0].createdAt !== rs0.data.createdAt
      ) {
        let rs = await axios.get(`${this.$store.state.server}/payments`);
        this.requests = rs.data;
        playAlert("submarine");
      }
    },
  },
  mounted() {
    this.getRequests();
    setInterval(() => {
      this.getRequests();
    }, 180000);
  },
};
</script>

<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
}

@media (min-width: 800px) {
  section {
    grid-template-columns: 1fr 1fr;
  }
}
</style>
